.vidList{
  padding-bottom: 20px;
  .inner{

    /* title */
    figure{
      padding: 60px 0; filter: grayscale(100%) ; position: relative; z-index: -1;
      background: url(/public/img/vidLIst_bg.jpg) no-repeat center/cover;
      margin-bottom: 40px;

      h1{
        font: 400 120px/1 'Oswald'; color: #222;
        text-align: center; 
      }  
    }





    .wrap{
      display: flex; justify-content: space-between;

      /* section - youtube API vid list */
      .list{
        width: 708px; margin: 0 auto;
        margin: 0 20px; 

        article{
          width: 100%;
          display: flex; justify-content: center; align-items: flex-end; flex-wrap: wrap;
          margin-bottom: 80px;

          &:nth-of-type(2n) {flex-direction: row-reverse;}

          /* pic - youtube image */
          .pic{
            width:413px;
            height: 250px;
            background: url(/src/img/Film2.jpg) no-repeat center/cover;
            cursor: pointer; margin-bottom: 20px;

            img{
              width: 100%; height: 100%; object-fit: cover; 
              padding: 35px 0px;
              opacity: 0.6; transition: 0.5s;
            }
          }

          /* icon*/
          .movieIcon{
            width: 256px; padding: 0px 10px;
            height: 250px;
            
            display: flex; justify-content: center; align-items: center;
            .popcon{
              width: 30%; height: 40%;
              background: url(/public/img/youtube1.png) center/cover;
            }
            .screen{
              width: 30%; height: 40%;
              background: url(/public/img/youtube2.png) center/cover;
            }
            .camera{
              width: 30%; height: 40%;
              background: url(/public/img/youtube5.png) center/cover;
            }
          }


          /* ticket box - main ticket, side ticket */
          .txt{
            width: 100%;
            height: 250px;
            position: relative;
            background: #F9EDC5;
            opacity: 0.8;

            /* mainTick - youtube api data */
            .mainTick{
              display: grid;
              grid-template-rows: 2fr 4fr 2fr 2fr 4fr 1fr ;
              grid-template-columns: 1fr 1fr 1fr  2.5fr;
              width: calc(100% - 250px);
              height: 250px;
              padding: 10px 5px 10px 10px;
              border-right: 2px dashed #34445C;

              grid-template-areas: 'icon tickName tickName tickName '
                                            'icon descYoutb descYoutb descYoutb'
                                            'titleYoutb titleYoutb titleYoutb titleYoutb'
                                            'star star star price'
                                            'date seat time price'
                                            'tickNum tickNum tickNum tickNum';
              
              >div{
                border: 1px solid #34445C;
                padding: 5px;

                &.icon{
                  grid-area: icon; 
                  img{  width: 100%; height: 100%; object-fit: cover; }
                }
                &.tickName{
                  grid-area: tickName;
                  p{
                    text-transform: uppercase;
                    font: 18px "Abril Fatface";
                    letter-spacing: 1px;
                    text-align: center;
                  }
                }
                &.descYoutb{
                  grid-area: descYoutb;
                  p{
                    font: 400 14px/1.1 'Oswald';
                    overflow: hidden;
                    text-align: center; overflow: hidden;
                  }
                }
                &.titleYoutb{
                  grid-area: titleYoutb;
                  h2{
                    font: 20px/1 'Abril Fatface';
                    text-align: center;
                    color: #7C2617; 
                  }
                }
                &.star{
                  grid-area: star;
                  background: #34445C;
                  text-align: center;
    
                  span{
                    display: inline-block;
                    margin-right: 20px;
                    color: #F9EDC5;
                    &:nth-of-type(3) {margin-right: 0;}
                  }
                }
                &.date{
                  grid-area: date;
                  p{
                    font: 400 14px/1 'Oswald';
                    margin-bottom: 10px; text-transform: uppercase;
                    color: #555;
                  }
                  span{
                    display:block;
                    font: 700 13px/1 'Oswald';
                    text-align: center;
                    color: #34445C;
                  }
                }
                &.seat{
                  grid-area: seat;
                  p{
                    font: 400 14px/1 'Oswald';
                    text-transform: uppercase; 
                    color: #555;
                  }
                  span{
                    display:block;  padding: 5px 0;
                    font: 700 17px/1 'Oswald';
                    text-align: center;
                    color: #34445C;
                  }
                }
                &.time{
                  grid-area: time;
                  p{
                    font: 400 14px/1 'Oswald';
                    text-transform: uppercase;
                    color: #555;
                  }
                  span{
                    display:block; padding: 5px 0;
                    font: 700 17px/1 'Oswald';
                    text-align: center;
                    color: #34445C;
                  }
                }
                &.price{
                  grid-area: price;
                  display: flex; flex-wrap: wrap;
                  span{
                    font: 400 14px/1 'Oswald';
                    text-transform: uppercase;
                    color: #555;
                    margin-right: 20px;
                  }
                  strong{
                    font: 36px/1 'Abril Fatface';
                    color: #7C2617; margin-bottom: 10px;
                  }
                  p{
                    width: 100%;
                    font: 22px/1 'arial';
                    text-align: center; 
                  }
                }
                &.tickNum{
                  grid-area: tickNum;
                  p{
                    font: 10px/1 'arial';
                    color: #555; text-align: center;
                  }
                }
              }
            }

            /* sideTick */
            .sideTick{
              width: 100%; 

              .wrap{
                float: right;
                width: 250px; height: 250px;
                //background: rgba(0, 0, 255, 0.192);
                padding: 10px 10px 30px;
                transform: rotate(-90deg);
                transform-origin: left top;
                display: grid;
                grid-template-rows:  2fr 2fr 2fr 1fr;
                grid-template-columns: 1fr 1fr 1fr;
                grid-template-areas: 'admit admit admit'
                                              'greatest greatest greatest'
                                              'date seat time'
                                              'tickNum tickNum tickNum';

                >div{
                  width: 100%; height: 100%;
                  border: 1px solid #34445C;
                  padding: 5px;

                  &.admit{
                    grid-area: admit;
                    P{
                      font: 34px/1 'arial'; padding: 10px 0 0;
                      letter-spacing: 2px;
                      color: #34445C; text-align: center; 
                    }

                  }
                  &.greatest{
                    grid-area: greatest;
                    p{
                      text-transform: uppercase; letter-spacing: 2px;
                      font: 14px "Abril Fatface";
                      letter-spacing: 1px; padding: 5px 0 ;
                      text-align: center; color: #7C2617;
                    }
                  }
                  &.date{
                    grid-area: date;
                    p{
                      font: 400 14px/1 'Oswald';
                      margin-bottom: 10px; text-transform: uppercase;
                      color: #555;
                    }
                    span{
                      display:block;
                      font: 700 13px/1 'Oswald';
                      text-align: center;
                      color: #34445C;
                    }
                  }
                  &.seat{
                    grid-area: seat;
                    p{
                      font: 400 14px/1 'Oswald';
                      text-transform: uppercase; 
                      color: #555;
                    }
                    span{
                      display:block;  padding: 5px 0;
                      font: 700 17px/1 'Oswald';
                      text-align: center;
                      color: #34445C;
                    }
                  }
                  &.time{
                    grid-area: time;
                    p{
                      font: 400 14px/1 'Oswald';
                      text-transform: uppercase;
                      color: #555;
                    }
                    span{
                      display:block; padding: 5px 0;
                      font: 700 17px/1 'Oswald';
                      text-align: center;
                      color: #34445C;
                    }
                  }
                  &.tickNum{
                    grid-area: tickNum;
                    p{
                      font: 12px/1 'arial';padding: 5px 0 0px;
                      color: #555; text-align: center; 
                    }
                  }
                }
              }
            }
          }

          &:hover{
            .pic img {opacity: 1;}
            .txt {opacity: 1;}
          }
        }
      }

      /* section - posters */
      .posters{
        width: 432px; padding: 0 20px;
        border-left: 1px solid #000;
      }
    }
    /* popup */
    .pop{
      width: 100%; height: 100vh;
      position: fixed; top: 0px; left: 0px; z-index: 1;
      background: rgba(0,0,0,0.9);
      padding: 5vw;

      iframe{
        border: none;
        width: 100%; height: 100%;
      }
      
      span{
        position: absolute; top: 3vw; right: 5vw;
        font-size: 20px; color: #fff;
        cursor: pointer;
      }

      p{
        position: absolute; top: 50%; left: 50%; z-index: -1;
        transform: translate(-50%, -50%);
        font:30px/1 'Abril Fatface';
        color: #fff;
      }
    }
  }
}

/* tablet*/
@media screen and (max-width: $tablet){
  .youtube{
    .inner{
      .title_wrap h1{  font-size: 12vw!important; }

      /* youtube vid list*/
      .list{
        width: 708px; margin: 0 auto;
        article{
          width: 100%;
          margin-bottom: 60px;
        }
      }
    }
  }
}

@media screen and (max-width: 780px) {
  .youtube .inner .list{width: 100%;}
  .youtube .inner .list article{
    width: 100%;
    align-items: center;
    
    /* icon*/
    .movieIcon{  display: none;}
  
    .txt{
      width: 100%;

      .mainTick{
        width: 100%;
        padding: 10px 20px;
        border-right: none;
        >div{
          &.date, &.seat, &.time{
            span{ font-size: 12px}
          }
        }
      }
      .sideTick{
        display: none;
      }
    }
  }
}

/* mobile */
@media screen and (max-width: $mobile){
  .youtube{
    .inner{
      /* title */
      .title_wrap h1{  font-size: 10vw!important; }

      /* youtube vid list*/
      .list{
        article{
          width: 100%; padding: 0px 10px;
          margin-bottom: 40px;

          .pic{
            max-width: 415px; width: 100%;
          }

          .txt{
            height: auto; 

            .mainTick{
              height: auto;

              grid-template-areas: 'tickName tickName tickName tickName '
              'descYoutb descYoutb descYoutb descYoutb'
              'titleYoutb titleYoutb titleYoutb titleYoutb'
              'star star star price'
              'date seat time price'
              'tickNum tickNum tickNum tickNum';

              >div{
                &.icon{
                  display: none;
                }

                &.tickName, &.descYoutb{
                  display: flex; justify-content: center; align-items: center;
                }
              }
            }
          }
        }
      }
    }
  }
}